.container {
  width: 370px;
}

.friendContainer {
  height: 650px;
  overflow-y: scroll;
  padding: 0 5px 0 0;
  margin-top: 20px;
}

.friendContainer::-webkit-scrollbar {
  width: 4px; /* */
}

.friendContainer::-webkit-scrollbar-thumb {
  background-color: rgb(87, 214, 150); /* */
  border-radius: 10px;
}

.friendContainer::-webkit-scrollbar-track {
  background-color: #f4f9e5; /*  */
}
